.fk-button-icon {
  border: none;
  color: $snow;
  background: transparent;
  transition: none;
  &:hover,
  &:focus,
  &:focus-visible {
    box-shadow: none;
    border: none;
    color: $snow;
    background: transparent;
    box-shadow: none;
    outline: none;

    i {
      color: $snow;
    }
  }

  i {
    color: $stone;
  }
}

.fk-button-action {
  color: $snow;
  background-color: $blue-dark;
  &:hover {
    background-color: $blue;
  }
  &:disabled {
    color: $slate;
    background-color: $raven;
    cursor: not-allowed;
  }
}

.fk-button-action-progress {
  color: $snow;
  background-color: $blue-dark;
  opacity: $opacity-blur;
  cursor: progress;
}

.fk-button-primary {
  color: $onyx;
  background-color: $dust;
  &:hover {
    background-color: $snow;
  }
  &:disabled {
    color: $slate;
    background-color: $raven;
    cursor: not-allowed;
  }
}

.fk-button-secondary {
  color: $dust;
  background-color: $raven;
  &:hover {
    color: $snow;
    background-color: $onyx;
  }
  &:disabled {
    color: $coal;
    background-color: $onyx;
    cursor: not-allowed;
  }
}
